<template>	
<section class="wrap has_header_wrap c_wrap">
	
	<!-- 头部标题 -->
	<com-header clear-data='true' v-on:clearData="clearAddData" titles="新建收货地址">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->

	<!-- 地址列表 -->
	<div class="edit_address">
		<mt-field label="收货人" placeholder="收货人姓名" v-model.tirm="username"></mt-field>
		<mt-field label="联系电话" placeholder="收货联系电话" type="tel" v-model.tirm="phone"></mt-field>
		<div class="address_choose choose_rea" @click="chooseArea">
			<mt-cell title="所在地区" :value="curSelectAdd | areaFilter" is-link></mt-cell>
		</div>
		<!-- <mt-field label="详细地址" placeholder="收货详细地址" type="textarea" rows="4" v-model.tirm="area_detail"></mt-field> -->
		<mt-field label="详细地址" placeholder="收货详细地址" type="text" v-model.tirm="area_detail"></mt-field>
	</div>
	<!-- <vue-area :props-show="show" :props-result="areas" v-on:result="areaResult"></vue-area> -->
	<!-- 地址列表 end -->
	
	<!-- <mt-picker :slots="slots" @change="onValuesChange" value-key="name"></mt-picker> -->
	
	<mt-popup
		position="bottom"
	  	v-model="popupVisible"
  		popup-transition="popup-fade">
  	  <div class="btns d_flex">
  	  	<mt-button size="small" @click="closePopupVisible">取消</mt-button>
  	  	<mt-button size="small" @click="closePopupVisible(1)">确定</mt-button>
  	  </div>	
      <div class="province pickerWrapper">
        <mt-picker :slots="provinces" @change="onProvinceChange" value-key="name"></mt-picker>
      </div>
      <div class="city pickerWrapper">
        <mt-picker :slots="citys" @change="onCityChange" value-key="name"></mt-picker>
      </div>
      <div class="area pickerWrapper">
        <mt-picker :slots="areas2" @change="onAreaChange" value-key="name"></mt-picker>
      </div>
    </mt-popup>
 <!-- {{ curSelectAdd }} -->
	<!-- 添加新地址 -->
	<button class="foot_btn" @click="jumpAdd"><span>保存</span></button>
	<!-- 添加新地址 end -->

</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle';
import comFoot from '@/components/Common/ComFoot';
import { Field, Cell, Button, MessageBox, Popup, Picker, Toast } from 'mint-ui';
import vueArea from 'vue-area';

export default {
	name: 'Add_address',
	data (){
		return {
			username: '', //收货人姓名
			phone: '', //收货联系电话
			areas: '', //所在地区
			// area_detail: this.$store.state.edAddInfo?this.$store.state.edAddInfo.address:'', //收货详细地址
			area_detail: '', //收货详细地址
			popupVisible: false,//显示选择地址
    		show: false,
    		flag: false,//防止多次提交
    		area: '',//地区对象
    		city: '',//城市对象
    		province: '',//区域对象
    		addressId: this.$route.query.addressId,//编辑地址ID
    		isDefault: 0,//是否为默认地址 默认0 不是
    		slots: [
				{ //0
				  flex: 1,
				  values: [],
				  className: 'slot1',
				  textAlign: 'left'
				}, 
				{ //1
				  divider: true,
				  content: '-',
				  className: 'slot2'
				}, 
				{ //2
				  flex: 1,
				  values: [],
				  className: 'slot3',
				  textAlign: 'left'
				}, 
				{ //3
				  divider: true,
				  content: '-',
				  className: 'slot2'
				}, 
				{ //4
				  flex: 1,
				  values: [],
				  className: 'slot4',
				  textAlign: 'left'
				}
	      	],
	      	provinces: [
	      		{ //0
				  flex: 1,
				  values: [{'id':0, 'name':'请选择'}],
				  className: 'slot1',
				  textAlign: 'left',
				  defaultIndex: 0
				}
	      	],
	      	citys: [
	      		{ //0
				  flex: 1,
				  values: [{'id':0, 'name':'请选择'}],
				  className: 'slot2',
				  textAlign: 'center',
				  defaultIndex: 0
			  	}
	      	],
	      	areas2: [
	      		{ //0
				  flex: 1,
				  values: [{'id':0, 'name':'请选择'}],
				  className: 'slot1',
				  textAlign: 'right',
				  defaultIndex: 0
			  	}
	      	],
	      	curSelectAdd: {
	      		'provinces':'',
	      		'citys': '',
	      		'areas': ''
	      	},
	      	editInfo: '',//编辑的地址信息
		}
	},
	components: {
		comHeaderTitle, // 公共头部标题
		'mt-field': Field,
		'mt-button': Button,
		'mt-cell': Cell,
		'mt-popup': Popup,
		'mt-picker': Picker,
		vueArea,
	},
	filters: {
		//所在地区
		areaFilter ( data ){
			if( data.provinces[0] ){
				if(data.provinces[0].id === 0){
					return '请选择所在地区';
				}else{
					return data.provinces[0].name + ' ' + data.citys[0].name + ' ' + data.areas[0].name;
				}
			}
		},
	},
	created (){
		const _self = this;

		if( this.addressId ){ //编辑地址
			this.$ajax({
				method: 'get',
				// url: '/apiserver/delivery/getDeliveryAddressByMemberID?memberID=10000&addressID='+_self.addressId,
				url: _self.webRoot + '/delivery/getDeliveryAddressByMemberID?memberID='+ _self.$store.state.memberId +'&addressID='+_self.addressId,
			})
			.then(function(res){
				// console.log('编辑地址： ', res)
				if( res.data.success ){
					// _self.curAddress = res.data.data;
					_self.phone = res.data.data.mobile;
					_self.area_detail = res.data.data.address;
					_self.username = res.data.data.contactMan;
					_self.isDefault = res.data.data.isDefault;
				}else{
					_self.$options.methods.showToast('获取地址失败!');
					return false;
				}
			})
			.catch(function(err){
				console.log(err)
			});
		};

	},
	mounted (){
		const _self = this;

		//编辑地址
		if( _self.$store.state.edAddInfo ){
			//获取地址 省
			this.$ajax({
				// url: '/apiserver/selectArea',
				url: _self.webRoot + '/selectArea',
			})
			.then(function(res){
				// console.log('获取地址 省: ', res)
				_self.provinces[0].values = res.data;

				//编辑 - 传递过过来的数据
				if( _self.$store.state.edAddInfo ){
					for( let x in res.data ){
						if(res.data[x].id == _self.$store.state.edAddInfo.districtID){
							_self.provinces[0].defaultIndex = Number(x);
							break;
						}
					}
				}
			})
			.catch(function(err){
				console.log(err)
			});
		}
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//省份改变
		onProvinceChange(picker, values){
			const _self = this;
			//获取市
			if( !picker.getSlotValue(0) || values[0].id === 0 ){ return }

			this.curSelectAdd.provinces = values;
			_self.$ajax({
				// url: '/apiserver/selectArea',
				url: _self.webRoot + '/selectArea',
				data: {parentId: picker.getSlotValue(0).id}
			})
			.then(function(res){
				// console.log(222,res)
				_self.citys[0].values = res.data;
				//编辑 - 传递过过来的数据
				if( _self.$store.state.edAddInfo ){
					for( let x in res.data ){
						if(res.data[x].id == _self.$store.state.edAddInfo.city){
							_self.citys[0].defaultIndex = Number(x);
							break;
						}
					}
				}
			})
			.catch(function(err){
				console.log(err)
			});
		},
		//城市改变
		onCityChange(picker, values){
			const _self = this;
			// console.log('区picker: ', values)

			this.curSelectAdd.citys = values;
			//获取区
			if( !picker.getSlotValue(0) || values[0].id === 0 ){ return }
			_self.$ajax({
				// url: '/apiserver/selectArea',
				url: _self.webRoot + '/selectArea',
				data: {parentId: picker.getSlotValue(0).id}
			})
			.then(function(res){
				// console.log(333,res)
				_self.areas2[0].values = res.data;
				//编辑 - 传递过过来的数据
				if( _self.$store.state.edAddInfo ){
					for( let x in res.data ){
						if(res.data[x].id == _self.$store.state.edAddInfo.county){
							_self.areas2[0].defaultIndex = Number(x);
							break;
						}
					}
				}
			})
			.catch(function(err){
				console.log(err)
			});
		},
		//地区改变
		onAreaChange(picker, values){
			const _self = this;

			this.curSelectAdd.areas = values;
			if( !picker.getSlotValue(0) ){ return }
			// console.log(values)
		},
		onValuesChange(picker, values) {

	    },
		//选择地区
	    chooseArea (){
	    	const _self = this;
	    	// this.show = true ;
			//获取地址 省
			this.$ajax({
				// url: '/apiserver/selectArea',
				url: _self.webRoot + '/selectArea',
			})
			.then(function(res){
				// console.log('选择地区: ', res)
				_self.provinces[0].values = res.data;
			})
			.catch(function(err){
				console.log(err)
			});
	    	this.popupVisible = true;
	    },
	    //点击选择地区触发
	    areaResult (show, result){
	        this.show = show ; 
	        this.areas = result ;

	        // console.log('show: ', show);
	        // console.log('result:' , result);
	        this.area = result.area;
	        this.city = result.city;
	        this.province = result.province;
	    },

		// 保存
		jumpAdd (){
			const _self = this;

			if(this.flag)return;
			this.flag = true;

			if( _self.curSelectAdd.provinces[0].id === 0 ){
				this.$options.methods.showToast('请填写收货地址!');
				this.flag = false;
				return false;
			}
			
			if( !this.username ){
				this.$options.methods.showToast('请填写收货人!');
				this.flag = false;
				return false;
			}
			//省市区中文
			// var ssq = _self.curSelectAdd.provinces[0].name + _self.curSelectAdd.citys[0].name + _self.curSelectAdd.areas[0].name;
			this.$ajax({
				// url: '/apiserver/delivery/addDeliveryAddress',
				url: _self.webRoot + '/delivery/addDeliveryAddress',
				data: {
					memberID: _self.$store.state.memberId,
					isDefault: 0, //是否默认地址
					countryID: 1, //国家编码, 默认中国
					// districtID: _self.area.code, //省、直辖市
					// city: city, //市县
					// county: province, //区、县
					districtID: _self.curSelectAdd.provinces[0].id, //省、直辖市
					city: _self.curSelectAdd.citys[0].id, //市县
					county: _self.curSelectAdd.areas[0].id, //区、县
					// address: ssq + _self.area_detail,//街道地址
					address: _self.area_detail,//街道地址
					mobile: _self.phone,//移动电话
					contactMan: _self.username,//联系人
					addressID: _self.addressId,//编辑地址ID 不是编辑时不传
					isDefault: _self.isDefault,//是否为默认地址
					// Zip: '', //邮政编码
					// telephone: '', //固定电话
					// remark: '', //签名
					// curSelectAdd
				},
			})
			.then(function(res){
				// console.log(res)
				if( res.data.success === true ){
					_self.$options.methods.showToast('添加成功!', 1500);
					setTimeout(function(){
						_self.$router.go(-1);
					},1500);
				}else{
					_self.$options.methods.showToast(res.data.msg);
				}
				_self.flag = false;
			})
			.catch(function(err){
				console.log(err)
				_self.flag = false;
			});
		},
		//清除vuex编辑地址信息
		clearAddData (){
			this.$store.commit('editAddressInfo', '')
			// console.log('清除数据')
		},

		//公共函数
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    //关闭选择地址
	    closePopupVisible ( type ){
	    	this.popupVisible = false;
	    	//点击确定
	    	if( type === 1 ){
    			this.area_detail = '';
	    		return false;
	    	}
	      	this.curSelectAdd.provinces = this.curSelectAdd.citys = this.curSelectAdd.areas = '';
	    }
	},
}	
</script>

<style scoped>
@import '../../../static/css/childre_wrap.css';
@import './Css/c_style.css';
</style>
<style>	
/* edit_address 新增、编辑地址 */
.edit_address .mint-cell-text {
	font-size: 0.4375rem;
	color: #262626;
}
.edit_address .mint-cell-value {
	font-size: 0.34375rem;
	color: #a7a7a7;
}
.edit_address .mint-cell-value input {
	color: #a7a7a7;
}
.edit_address .mint-cell-value input::-webkit-input-placeholder { color:#a7a7a7; }
.edit_address .mint-cell-value input::-moz-placeholder { color:#a7a7a7; } /* firefox 19+ */
.edit_address .mint-cell-value input:-ms-input-placeholder { color:#a7a7a7; } /* Internet Explorer 10+ */
.edit_address .mint-cell-value input:-moz-placeholder { color:#a7a7a7; } /* firefox 14-18 */
.edit_address .mint-cell-value textarea::-webkit-input-placeholder { color:#a7a7a7; }
.edit_address .mint-cell-value textarea::-moz-placeholder { color:#a7a7a7; } /* firefox 19+ */
.edit_address .mint-cell-value textarea:-ms-input-placeholder { color:#a7a7a7; } /* Internet Explorer 10+ */
.edit_address .mint-cell-value textarea:-moz-placeholder { color:#a7a7a7; } /* firefox 14-18 */

.mint-popup-bottom {
	width: 100%;
}

/* address_choose choose_rea */
.address_choose.choose_rea .mint-cell-value {
	max-width: 6.25rem;
}
/* 所有地区 */
.ProvCityHeaderCancle {
	color: #2fba67;
	font-size: 0.5rem !important;
}
.ProvCityHeaderConfirm {
	color: #2fba67;
	font-size: 0.5rem !important;
}

.mint-popup .pickerWrapper {
	float:left;
	width:33.3333%;
}
.mint-popup .pickerWrapper .picker-slot {
	font-size:  0.4375rem;
}
.mint-popup .btns {
	padding: 0 0.3125rem;
    border-bottom: 0.03125rem solid #eee;
}
.mint-popup .btns .mint-button {
	font-size:  0.4375rem;
	background-color: rgba(255,255,255,0);
	border: 0;
	box-shadow: none;
}
/* .mint-popup .picker-item {
	height: 1.125rem !important;
	line-height: 1.125rem !important;
} */
</style>


